* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.masthead {
  background-color: #0000;
  background-image: linear-gradient(90deg, #ffffff1a 0% 100%), url("bg-masthead.9c2d83f8.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  width: 100%;
  height: auto;
  min-height: 30rem;
  padding-top: 8rem;
  padding-bottom: 8rem;
  display: table;
  position: relative;
}

.masthead h1, .masthead .h1 {
  margin: 0;
  padding: 0;
  font-size: 4rem;
}

@media (width >= 992px) {
  .masthead {
    height: 100vh;
  }

  .masthead h1, .masthead .h1 {
    font-size: 5.5rem;
  }
}

.scroll-to-top {
  text-align: center;
  color: #fff;
  background: #343a4080;
  width: 50px;
  height: 50px;
  line-height: 50px;
  display: none;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.scroll-to-top:focus, .scroll-to-top:hover {
  color: #fff;
}

.scroll-to-top:hover {
  background: #343a40;
}

.scroll-to-top i {
  font-weight: 800;
}

#sidebar-wrapper {
  z-index: 2;
  background: #343a40cc;
  border-left: 1px solid #ffffff1a;
  width: 250px;
  height: 100%;
  transition: all .4s;
  position: fixed;
  right: 0;
  transform: translateX(250px);
}

.sidebar-nav {
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 0;
}

.sidebar-nav li.sidebar-nav-item a {
  color: #fff;
  padding: 15px;
  text-decoration: none;
  display: block;
}

.sidebar-nav li a:hover {
  color: #bdbdbd;
  background: #fff3;
  text-decoration: none;
}

.sidebar-nav li a:active, .sidebar-nav li a:focus {
  text-decoration: none;
}

.sidebar-nav > .sidebar-brand {
  background: #343a401a;
  height: 80px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  font-size: 1.2rem;
  line-height: 50px;
}

.sidebar-nav > .sidebar-brand a {
  color: #fff;
  text-decoration: none;
}

.sidebar-nav > .sidebar-brand a:hover {
  color: #fff;
  background: none;
}

#sidebar-wrapper.active {
  width: 250px;
  transition: all .4s;
  right: 250px;
}

.menu-toggle {
  text-align: center;
  color: #fff;
  z-index: 999;
  background: #343a4080;
  width: 50px;
  height: 50px;
  line-height: 50px;
  position: fixed;
  top: 15px;
  right: 15px;
}

.menu-toggle:focus, .menu-toggle:hover {
  color: #fff;
}

.menu-toggle:hover {
  background: #343a40;
}

.main-content {
  flex: 1;
  margin-right: 250px;
  padding: 2rem;
}

.btn-x2 {
  color: #fff;
  background: none;
  outline: 2px #fff;
  padding: .25rem 1.25rem;
}

.btn-x2:hover {
  color: #bdbdbd;
  background: none;
}

.btn-xl {
  background: #343a40;
  outline: 2px solid #0000;
  padding: 1.25rem 2.5rem;
  transition: background .3s ease-in-out;
}

.btn-xl:hover {
  background: #3ca55c;
}

.btn-xl:active {
  outline: 2px solid #0000;
}

body {
  color: #2c3e50;
  font-family: Calibri;
  line-height: 1.6;
}

header {
  background: #3a3a3a;
  padding: 1rem 0;
}

header nav {
  justify-content: left;
  align-items: left;
  background: linear-gradient(to right, #3ca55c, #b5ac49);
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  display: grid;
}

header nav .logo img {
  height: 50px;
}

header nav ul {
  list-style: none;
  display: flex;
}

header nav ul li {
  margin-left: 2rem;
}

header nav ul li a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

header nav ul li a:hover {
  color: #e6e6e6;
}

.chart {
  max-width: 900px;
  height: 450px;
  margin: 0 auto;
  padding: 50px;
